import SaveIcon from '@mui/icons-material/Save'
import { LoadingButton } from '@mui/lab'
import { Button, Checkbox, Stack, TextField, Typography } from '@mui/material'
import { WebAuthorAPI } from 'API/author'
import DeleteButton from 'components/Button/Delete'
import { IAccount, PERMISSION } from 'interfaces'
import { useEffect, useMemo, useState } from 'react'
import { MdOutlinePersonRemove } from 'react-icons/md'
import { Link, useLocation, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getGroups } from 'redux/slices/Group'
import { UIAction } from 'redux/slices/UI'
import { getWebAuthors } from 'redux/slices/WebAuthor'

export default function Update() {
	const { id } = useParams<{ id: string }>()
	const dispatch = useAppDispatch()
	const location = useLocation()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)

	const status = useAppSelector((s) => s.WebAuthor.status)
	const author = useAppSelector((s) => s.WebAuthor.list).find((f) => f.id === parseInt(id || ''))
	useEffect(() => {
		if (status === 'init') dispatch(getWebAuthors())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	const [search, setSearch] = useState('')
	const groupStatus = useAppSelector((s) => s.Group.status)
	useEffect(() => {
		if (groupStatus === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const [loading, setLoading] = useState(false)
	const [selectedAccount, setSelectedAccount] = useState<IAccount[]>([])
	useEffect(() => {
		if (selectedAccount.length === 0 && author) {
			setSelectedAccount(author.accounts)
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [author])

	const account = useAppSelector((s) =>
		s.Group.list.filter((f) => f.id !== 1 && f.permission[PERMISSION.CMS] > 4)
	).reduce((p, c) => [...p, ...c.accounts], [] as IAccount[])
	const canSL = useMemo(() => {
		return account.filter((f) => f.email.includes(search) || f.name.includes(search))
	}, [account, search])

	if (!id || !tokenInfo || !author) return null
	return (
		<Stack gap={1} my={2}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					color="primary.dark"
					variant="h5"
					sx={{
						fontWeight: 600,
					}}
				>
					承認メール設定
				</Typography>
				<Link to={`/author/${id}/delete`} state={{ location }}>
					<DeleteButton text="削除" sx={{ width: 150, py: '2px', fontSize: 14 }} />
				</Link>
			</Stack>
			<Stack alignItems="stretch" gap={1}>
				<Stack
					gap={1}
					flex={1}
					sx={{
						border: '1px solid #ccc',
						p: 0.5,
					}}
				>
					<Stack sx={{ bgcolor: '#eee', p: 1, maxHeight: 300, overflow: 'auto' }} gap={1}>
						<Typography>選択した承認者</Typography>
						<Stack direction="row" flexWrap="wrap" gap={1}>
							{selectedAccount.map((v) => {
								return (
									<Stack
										key={v.id}
										direction="row"
										alignItems="center"
										sx={{
											border: '1px solid #ccc',
											px: 0.5,
										}}
									>
										<Typography variant="caption">{v.email}</Typography>
										<Button
											color="error"
											sx={{
												minWidth: 0,
											}}
											onClick={() => {
												const tmp = selectedAccount.filter((f) => f.id !== v.id)
												setSelectedAccount(tmp)
											}}
										>
											<MdOutlinePersonRemove />
										</Button>
									</Stack>
								)
							})}
						</Stack>
					</Stack>
					<Stack
						sx={{
							border: '1px solid #ccc',
							height: '50vh',
							overflow: 'auto',
							p: 0.5,
						}}
					>
						<Typography>承認可能者</Typography>
						<Stack p={0.5}>
							<TextField
								variant="outlined"
								margin="dense"
								fullWidth
								size="small"
								label={'フィルター'}
								InputLabelProps={{ shrink: true }}
								value={search}
								onChange={(e) => setSearch(e.target.value)}
							/>
						</Stack>
						{canSL.map((v) => {
							return (
								<Stack key={v.id} px={0.5}>
									<Stack direction="row" alignItems="center" gap={0.5}>
										<Checkbox
											size="small"
											sx={{
												p: 0,
											}}
											checked={!!selectedAccount.find((f) => f.id === v.id)}
											onChange={(e) => {
												const checked = e.target.checked
												if (checked) {
													setSelectedAccount([...selectedAccount, v])
												} else {
													const tmp = selectedAccount.filter((f) => f.id !== v.id)
													setSelectedAccount(tmp)
												}
											}}
										/>
										<Typography variant="caption">{v.email}</Typography>
									</Stack>
								</Stack>
							)
						})}
					</Stack>
				</Stack>
				<Stack>
					<LoadingButton
						sx={{
							display: 'flex',
							width: 160,
						}}
						disabled={selectedAccount.length === 0}
						loading={loading}
						loadingPosition="start"
						startIcon={<SaveIcon />}
						variant="outlined"
						color="warning"
						onClick={async () => {
							try {
								setLoading(true)
								const { error, message } = await WebAuthorAPI.update({
									email: author.email,
									userIds: selectedAccount.map((v) => v.id),
								})
								dispatch(
									UIAction.addGlobalMessage({
										type: error ? 'error' : 'success',
										message: message,
									})
								)
							} finally {
								setLoading(false)
							}
						}}
					>
						保存する
					</LoadingButton>
				</Stack>
			</Stack>
		</Stack>
	)
}
